import React, { useEffect, useState } from 'react';
import './configured-saml-idp.scss'
import { samlResponseData } from "../CommonInterfaces/idp-response-types";
import {
    ActionList,
    ActionListItem, Alert,
    Button,
    ClipboardCopy,
    Form,
    FormGroup,
    FormHelperText,
    HelperText,
    HelperTextItem,
    Label,
    Popover,
    Text,
    TextArea,
    TextContent,
    TextInput,
    TextVariants,
    Title
} from '@patternfly/react-core';
import CheckCircleIcon from '@patternfly/react-icons/dist/esm/icons/check-circle-icon';
import WrenchIcon from '@patternfly/react-icons/dist/esm/icons/wrench-icon';
import HelpIcon from '@patternfly/react-icons/dist/esm/icons/help-icon';
import styles from '@patternfly/react-styles/css/components/Form/form';
import { useChrome } from "@redhat-cloud-services/frontend-components/useChrome";
import { getSSOBaseUrl } from "../../../Utils/general-utils";
import { IdpTestingModal } from "../IdpTesting/idp-testing-modal";
import { DisableIdpModal } from "../Configured/disable-idp-modal";
import { DeleteIdpModal } from './delete-idp-modal';
import {SamlHelperTextMessages} from "../../../Apps/IdpSelfService/Constants/saml-helper-text-messages";

interface Props {
    samlData: samlResponseData,
    milestoneTwoEnabled: boolean
    recomputeAppState: () => void
}

const ConfiguredSamlIdp = ({ samlData, milestoneTwoEnabled, recomputeAppState }: Props) => {
    const chrome = useChrome();
    const ssoBaseUrl = getSSOBaseUrl(chrome);
    const [isTestingModalVisible, toggleTestingModalVisibility] = useState(false);
    const [isDisabledModalVisible, toggleIsDisabledModalVisible] = useState(false);
    const [isDeleteModalVisible, toggleIsDeleteModalVisible] = useState(false);

    return (
        <Form>
            {samlData.enabled &&
                <Alert variant="info" isInline title="This page currently shows a read-only version of your configuration.">
                    <p>Forthcoming enhancements will provide the ability to edit.</p>
                </Alert>
            }

            <Title headingLevel={"h1"} className="pf-v5-u-display-flex">
                {samlData.displayName}
                {samlData.enabled
                    ? <Label color="green" icon={<CheckCircleIcon />} className="pf-v5-u-ml-md">
                        Enabled
                    </Label>
                    : <Label color="grey" icon={<WrenchIcon />} className="pf-v5-u-ml-md">
                        Disabled
                    </Label>
                }
            </Title>

            <FormGroup
                label="Identity provider Entity ID"
                fieldId="saml-entity-id"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>{SamlHelperTextMessages.ENTITY_ID}</div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <TextInput isDisabled type="text" id="saml-entity-id" name="saml-entity-id" value={samlData.identityProviderEntityId} />
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{SamlHelperTextMessages.ENTITY_ID}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Single sign-on authentication request URL"
                fieldId="saml-authn-request-url">
                <TextInput isDisabled type="text" id="saml-authn-request-url" name="saml-authn-request-url" value={samlData.authenticationRequestsUrl} />
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{SamlHelperTextMessages.AUTHENTICATION_REQUEST_URL}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Service provider issuer"
                fieldId="saml-provider-issuer">
                <TextInput isDisabled type="text" id="saml-provider-issuer" name="saml-provider-issuer" value={samlData.serviceProviderEntityId} />
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{SamlHelperTextMessages.SERVICE_PROVIDER_ISSUER}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="x509 certificate"
                fieldId="saml-x509-certificate"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>A PEM certificate that is used to check for signatures.</div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <TextArea resizeOrientation="vertical" isDisabled type="text" id="saml-authn-request-url" name="saml-authn-request-url" value={samlData.x509Certificate} />
                <FormHelperText>
                    <HelperText>
                        {SamlHelperTextMessages.X509_CERTIFICATE.map((message, index) => (
                            <HelperTextItem key={index}>
                                <p dangerouslySetInnerHTML={{__html: message}}/>
                            </HelperTextItem>
                        ))}
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <Title headingLevel={"h3"}>URLs required for identity provider configuration</Title>

            <TextContent>
                <Text component={TextVariants.p}>
                    Use these URLs to assist in completing setup within your identity provider.
                </Text>
            </TextContent>

            <FormGroup
                label="Service provider metadata URL"
                fieldId="service-provider-metadata-url"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                Service provider metadata used to integrate Red Hat with your identity provider.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <ClipboardCopy
                    type="text"
                    id="service-provider-metadata-url"
                    name="service-provider-metadata-url"
                    isReadOnly>
                    {`${ssoBaseUrl}broker/${samlData.idpAlias}/endpoint/descriptor`}
                </ClipboardCopy>
            </FormGroup>

            <FormGroup
                label="Redirect URL / Assertion Consumption Service (ACS) URL"
                fieldId="redirect-url"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                The redirect URL to use when configuring your identity provider. This is where your identity provider will redirect users to authenticate to Red Hat.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <ClipboardCopy
                    type="text"
                    id="redirect-url"
                    name="redirect-url"
                    isReadOnly>
                    {`${ssoBaseUrl}broker/${samlData.idpAlias}/endpoint`}
                </ClipboardCopy>
            </FormGroup>

            {!milestoneTwoEnabled &&
                <ActionList>
                    <Popover
                        triggerAction="hover"
                        aria-label="Hoverable popover"
                        headerContent={<div>Read-Only</div>}
                        bodyContent={<div>This page currently shows a read-only version of your configuration. Forthcoming
                            enhancements will provide the ability to edit.</div>}
                        footerContent="">
                        <ActionListItem>
                            <Button isDisabled>
                                Save
                            </Button>
                        </ActionListItem>
                    </Popover>
                    <ActionListItem>
                        <Button isDisabled variant="link">
                            Cancel
                        </Button>
                    </ActionListItem>
                </ActionList>
            }

            {milestoneTwoEnabled &&
                <ActionList hidden={!milestoneTwoEnabled}>
                    <ActionListItem>
                        {!samlData.enabled
                            ? <Button onClick={() => toggleTestingModalVisibility(true)}>
                                Test and enable
                            </Button>
                            : <Button variant={"danger"} onClick={() => toggleIsDisabledModalVisible(true)}>
                                Disable
                            </Button>
                        }
                    </ActionListItem>
                    <ActionListItem>
                        <Button variant="link" onClick={() => toggleIsDeleteModalVisible(true)} isDanger>
                            Delete identity provider
                        </Button>
                    </ActionListItem>
                    {isDisabledModalVisible && <DisableIdpModal idpAlias={samlData.idpAlias} isOpen={isDisabledModalVisible} onClose={() => toggleIsDisabledModalVisible(false)} recomputeAppState={recomputeAppState} />}
                    {isTestingModalVisible && <IdpTestingModal idpAlias={samlData.idpAlias} isOpen={isTestingModalVisible} onClose={() => toggleTestingModalVisibility(false)} recomputeAppState={recomputeAppState} />}
                    {isDeleteModalVisible && <DeleteIdpModal idpAlias={samlData.idpAlias} isOpen={isDeleteModalVisible} onClose={() => toggleIsDeleteModalVisible(false)} recomputeAppState={recomputeAppState} />}
                </ActionList>
            }
        </Form>
    )
}

export default ConfiguredSamlIdp;
