import React, { MutableRefObject } from 'react';
import {
    WizardFooterWrapper /* data-codemods */, useWizardContext /* data-codemods */
} from '@patternfly/react-core/dist/esm/components/Wizard';
import { Button, Alert, AlertGroup } from '@patternfly/react-core';
import { isFailureTriggered } from "../../../Utils/general-utils";
import { SubmittableChoices, UserChoices } from "../CommonInterfaces/user-choices";
import {
    AUTH_REQUEST_URL,
    IDENTITY_PROVIDER_ENTITY_ID,
    X509_CERTIFICATE,
    SERVICE_PROVIDER_ISSUER
} from "../../../Apps/IdpSelfService/CommonInterfaces/idp-request-types";

import PropTypes from "prop-types";
import { useValidationContext } from "../CommonInterfaces/validation-context";
import { apiCreateIdp } from "../ApiClient/idp-controller";
import { useChrome } from "@redhat-cloud-services/frontend-components/useChrome";

interface Props {
    userChoiceData: MutableRefObject<UserChoices>;
    previous: Function;
    currentState: string;
}

const SubmitFooter = ({ userChoiceData, previous, currentState }: Props) => {
    const chrome = useChrome();
    const { goToNextStep, goToPrevStep } = useWizardContext();
    const [isLoading, setIsLoading] = React.useState(false);
    const [alerts, setAlerts] = React.useState<React.ReactNode>();
    const { validations, setValidations } = useValidationContext();

    /**
     * convert the choice data to use the key's required by the api.
     */
    const filterChoiceData = (): SubmittableChoices => {
        const provider = userChoiceData.current.selections.protocol === "OIDC_CONFIG" ? "oidc-improved" : "saml-improved"
        const idpEID = userChoiceData.current.selections[IDENTITY_PROVIDER_ENTITY_ID];
        const reqURL = userChoiceData.current.selections[AUTH_REQUEST_URL];
        const serviceEID = userChoiceData.current.selections[SERVICE_PROVIDER_ISSUER];
        const sanitizedCertChain = userChoiceData.current.selections[X509_CERTIFICATE].replace(/[\s\n\r]/gu, '');

        return {
            providerId: provider,
            identityProviderEntityId: idpEID,
            authenticationRequestsUrl: reqURL,
            serviceProviderEntityId: serviceEID,
            x509Certificate: sanitizedCertChain,
        }
    }

    /**
     * function that allows us to trigger the error alert on unsuccessful api call.
     */
    const throwError = (message: string) => {
        const timeout = 8000;
        // eslint-disable-next-line react/jsx-key
        setAlerts(<Alert variant="danger" isInline isPlain title={message} timeout={timeout} onTimeout={() => { setAlerts([]) }} />)
    }

    const createIDPCall = () => {
        apiCreateIdp(chrome, filterChoiceData())
            .then(response => response.json())
            .then((responseData) => {
                // errors that come through post authentication typically end up here
                if (responseData.error || responseData.error_description) {
                    console.error("Error Type: " + responseData.error);
                    console.error("Error Message: " + responseData.error_description);
                    throwError("Failed to upload config: " + responseData.error_description);

                    setIsLoading(false);
                }
                else {
                    setIsLoading(false)
                    goToNextStep();
                }

            })
            .catch(error => {
                // this handles any of the extraneous errors.
                throwError(error.toString());
                console.error('Error: ', error);
                setIsLoading(false);
            })
    }

    /**
     * Governs what happens when the next button gets clicked. Important for allowing the back and forth communication
     * between the footer element and the actual component being displayed when needing to perform steps such as validation.
     */
    const handleNextClick = () => {
        if (!isFailureTriggered(userChoiceData.current, currentState, validations, setValidations)) {
            // trigger the spinner.
            setIsLoading(true);
            // fire the call to SSO.
            createIDPCall();
        } else {
            setIsLoading(false);
        }
    }

    const handleBackClick = () => {
        goToPrevStep();
        // custom logic to update the current flow state.
        previous();
    }

    return (
        <WizardFooterWrapper>
            <Button variant="secondary" onClick={handleBackClick}>
                Back
            </Button>
            <Button onClick={handleNextClick} isLoading={isLoading}>Next</Button>
            <AlertGroup isLiveRegion className="pf-v5-u-mt-sm">
                {alerts}
            </AlertGroup>
        </WizardFooterWrapper>
    );
}

SubmitFooter.propTypes = {
    userChoiceData: PropTypes.object,
}

export default SubmitFooter;
