// Final page to display the urls of the flow https://miro.com/app/board/uXjVO_x__QE=/

import {Button, ClipboardCopy, Form, FormGroup, Popover, Text, Title} from '@patternfly/react-core';
import React, {useEffect, useState} from 'react';
import {useChrome} from "@redhat-cloud-services/frontend-components/useChrome";
import {getOrgId, getSSOBaseUrl} from "../../../Utils/general-utils";
import styles from "@patternfly/react-styles/css/components/Form/form";
import HelpIcon from "@patternfly/react-icons/dist/esm/icons/help-icon";
interface Props{
    accessToken: string | null;
}
const ImportSamlFromURL = ({accessToken} : Props) => {
    const [orgId, setOrgId] = useState('');
    const chrome = useChrome();

    const getConfigURL = (path: string) => {
        return getSSOBaseUrl(chrome) + path;
    };

    useEffect(() => {
        setOrgId(getOrgId(accessToken!));
    }, [])

    return (
        <Form>
            <Title headingLevel="h2">Configure these URLs in your Identity Provider</Title>
            <Text className="pf-v5-u-color-400 pf-v5-u-font-size-sm">Please review the following information and verify that these are entered where required in your organizations identity provider.</Text>

            <FormGroup
                label="Service provider metadata URL"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                Service provider metadata used to integrate Red Hat with your identity provider.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <ClipboardCopy isReadOnly clickTip="copied">
                    {getConfigURL(`broker/${orgId}/endpoint/descriptor`)}
                </ClipboardCopy>
            </FormGroup>

            <FormGroup
                label="Redirect URL / Assertion Consumption Service (ACS) URL"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                The redirect URL to use when configuring your identity provider. This is where your identity provider will redirect users to authenticate to Red Hat.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <ClipboardCopy isReadOnly clickTip="copied">
                    {getConfigURL(`broker/${orgId}/endpoint`)}
                </ClipboardCopy>
            </FormGroup>
        </Form>
    )
}

export default ImportSamlFromURL;
