import React from "react";
import { Button, Modal, ModalVariant } from "@patternfly/react-core";
import { apiToggleIdPStatus } from "../ApiClient/idp-controller";
import { useChrome } from "@redhat-cloud-services/frontend-components/useChrome";
import { ModalBody, ModalFooter } from "@patternfly/react-core/next";

interface Props {
    idpAlias: string,
    isOpen: boolean,
    onClose: () => void,
    recomputeAppState: () => void
}

export const DisableIdpModal = ({ idpAlias, isOpen, onClose, recomputeAppState }: Props) => {
    const chrome = useChrome();

    const disableIdp = async () => {
        await apiToggleIdPStatus(chrome, idpAlias);
        recomputeAppState();
        onClose();
    }

    return (
        <Modal
            titleIconVariant={'warning'}
            title="Disable identity provider?"
            isOpen={isOpen}
            variant={ModalVariant.small}
            onClose={onClose}
        >
            <ModalBody>
                Disabling your identity provider will allow users on your organization to sign-in using their Red Hat credentials
            </ModalBody>
            <ModalFooter>
                <Button variant="danger" onClick={() => disableIdp()}>
                    Disable
                </Button>
                <Button variant="link" onClick={onClose}>
                    Cancel
                </Button>
            </ModalFooter>
        </Modal>
    )
}
