import { getSSOApiUrl } from "../../../Utils/general-utils";
import { SubmittableChoices } from "../CommonInterfaces/user-choices";
import { ChromeAPI } from "@redhat-cloud-services/types";

function getSSOIdpApiUrl(chromeApi: ChromeAPI) {
    return getSSOApiUrl(chromeApi, 'apis/organizations/v1/identity-providers');
}

export async function apiGetIdp(chromeApi: ChromeAPI): Promise<Response> {
    try {
        const token = await chromeApi.auth.getToken();
        const response = await fetch(getSSOIdpApiUrl(chromeApi), {
            method: 'GET',
            referrerPolicy: 'no-referrer',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`
            }
        });

        if (!response.ok) {
            throw new Error(`Error: ${response.status} ${response.statusText}`);
        }

        return response;
    } catch (error) {
        console.error("Fetch failed:", error);
        throw error;
    }
}

export async function apiCreateIdp(chromeApi: ChromeAPI, createIdp: SubmittableChoices): Promise<Response> {
    try {
        const token = await chromeApi.auth.getToken();
        const response = await fetch(getSSOIdpApiUrl(chromeApi), {
            method: 'POST',
            referrerPolicy: 'no-referrer',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`
            },
            body: JSON.stringify(createIdp)
        });
        if (!response.ok) {
            throw new Error(`Error: ${response.status} ${response.statusText}`);
        }

        return response;
    } catch (error) {
        console.error("Create failed:", error);
        throw error;
    }
}

export async function apiToggleIdPStatus(chromeApi: ChromeAPI, idpAlias: String) {
    try {
        const token = await chromeApi.auth.getToken();
        const apiStatusUrl = getSSOIdpApiUrl(chromeApi) + `/${idpAlias}/status`;
        const response = await fetch(apiStatusUrl, {
            method: 'PATCH',
            referrerPolicy: 'no-referrer',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`
            },
            body: JSON.stringify({})
        })

        if (!response.ok) {
            throw new Error(`Error: ${response.status} ${response.statusText}`);
        }

        return response;
    } catch (error) {
        console.error("Status update failed:", error);
        throw error;
    }
}

export async function apiDeleteIdp(chromeApi: ChromeAPI, idpAlias: String) {
    try {
        const token = await chromeApi.auth.getToken();
        const deleteIdpUrl = getSSOIdpApiUrl(chromeApi) + `/${idpAlias}`;
        const response = await fetch(deleteIdpUrl, {
            method: 'DELETE',
            referrerPolicy: 'no-referrer',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`
            },
            body: JSON.stringify({})
        })

        if (!response.ok) {
            throw new Error(`Error: ${response.status} ${response.statusText}`);
        }

        return response;

    } catch (error) {
        console.error("IdP deletion failed:", error);
        throw error;
    }
}

