import React, {useEffect} from 'react';
import {
    TextInput,
    Form,
    FormGroup,
    HelperText,
    HelperTextItem, TextArea, Title, Popover, Button
} from '@patternfly/react-core';
import {UserChoices} from "@apps/IdpSelfService/CommonInterfaces/user-choices";
import {AUTH_REQUEST_URL, IDENTITY_PROVIDER_ENTITY_ID, X509_CERTIFICATE, SERVICE_PROVIDER_ISSUER} from "../../../Apps/IdpSelfService/CommonInterfaces/idp-request-types";
import {useValidationContext} from "../../../Apps/IdpSelfService/CommonInterfaces/validation-context";
import {FlowStep} from "../../../Apps/IdpSelfService/Constants/flow-step";
import styles from "@patternfly/react-styles/css/components/Form/form";
import HelpIcon from "@patternfly/react-icons/dist/dynamic/icons/help-icon";
import {SamlHelperTextMessages} from "../../../Apps/IdpSelfService/Constants/saml-helper-text-messages";

interface Props {
    updateStatus: Function;
    resetNextClicked: Function;
    rootUserChoices: UserChoices;
}
const ImportSamlManual = ({updateStatus, resetNextClicked, rootUserChoices}: Props) => {

    /**
     * updates the information being used to govern the application flow as well as the final api update object.
     * @param value
     */
    const handleSelection = (value: any) => {

        /*
         * regardless of current state. if the user alters the input clear existing flags. This should also reset
         * the currently displayed errors
         */
        resetNextClicked(false);

        //  update the main data storage object with the new value
        rootUserChoices.selections[value.target.id] = value.target.value;

        if (rootUserChoices.selections) { updateStatus(FlowStep.FINAL_STEP); }
    }

    const parseVariantValue = (value: string | undefined): ("default" | "error" | undefined) => {
        if (value === 'default' || value === 'error') { return value; }
        // eslint-disable-next-line
        return undefined;
    }


    const { validations, setValidations } = useValidationContext();

    useEffect(() => {
        const updated = {...validations};
        updated.IMPORT_SAML_MANUALLY =
            {
                'identityProviderEntityId': {
                    inputType: 'DOMAIN',
                    variant: 'default',
                    message: SamlHelperTextMessages.IDENTITY_PROVIDER_ENTITY_ID
                },
                'serviceProviderIssuer': {
                    inputType: 'GENERAL',
                    variant: 'default',
                    message: SamlHelperTextMessages.SERVICE_PROVIDER_ISSUER
                },
                'authenticationRequestsUrl': {
                    inputType: 'DOMAIN',
                    variant: 'default',
                    message: SamlHelperTextMessages.AUTHENTICATION_REQUEST_URL
                },
                'x509Certificate': {
                    inputType: 'x509',
                    variant: 'default',
                    message: SamlHelperTextMessages.X509_CERTIFICATE.join('@')
                }
            };
        setValidations(updated);
    }, []);

    return <Form>
        <Title headingLevel="h2">Configure SAML manually</Title>
        <FormGroup
            label="Identity provider Entity ID"
            isRequired
            onChange={(value) => { handleSelection(value) }}>
            <TextInput
                type="text"
                id={IDENTITY_PROVIDER_ENTITY_ID} />
            <HelperText>
                <HelperTextItem variant={ parseVariantValue(validations.IMPORT_SAML_MANUALLY?.identityProviderEntityId?.variant) }>
                    { validations.IMPORT_SAML_MANUALLY?.identityProviderEntityId?.message || SamlHelperTextMessages.IDENTITY_PROVIDER_ENTITY_ID }
                </HelperTextItem>
            </HelperText>
        </FormGroup>

        <FormGroup
            label="Authentication request url"
            isRequired
            onChange={(value) => { handleSelection(value) }}>
            <TextInput
                       type="text"
                       id={AUTH_REQUEST_URL} />
            <HelperText>
                <HelperTextItem variant={ parseVariantValue(validations.IMPORT_SAML_MANUALLY?.authenticationRequestsUrl?.variant) }>
                    { validations.IMPORT_SAML_MANUALLY?.authenticationRequestsUrl?.message || SamlHelperTextMessages.AUTHENTICATION_REQUEST_URL }
                </HelperTextItem>
            </HelperText>
        </FormGroup>

        <FormGroup
            label="Service provider issuer"
            isRequired
            onChange={(value) => { handleSelection(value) }}
            labelIcon={
                <Popover
                    triggerAction="hover"
                    bodyContent={
                        <div>{SamlHelperTextMessages.NO_SPECIAL_CHARS}</div>
                    }>
                    <Button
                        variant="plain"
                        type="button"
                        onClick={(e) => e.preventDefault()}
                        className={`customPopover ${styles.formGroupLabelHelp}`}>
                        <HelpIcon />
                    </Button>
                </Popover>
            }>
            <TextInput
                       type="text"
                       id={SERVICE_PROVIDER_ISSUER} />
            <HelperText>
                <HelperTextItem variant={ parseVariantValue(validations.IMPORT_SAML_MANUALLY?.serviceProviderIssuer?.variant) }>
                    { validations.IMPORT_SAML_MANUALLY?.serviceProviderIssuer?.message || SamlHelperTextMessages.SERVICE_PROVIDER_ISSUER }
                </HelperTextItem>
            </HelperText>
        </FormGroup>

        <FormGroup
            label="x509 certificate"
            isRequired
            onChange={(value) => { handleSelection(value) }}>
            <TextArea resizeOrientation="vertical"
                      type="text"
                      id={X509_CERTIFICATE}/>
            <HelperText>
                <HelperTextItem variant={ parseVariantValue(validations.IMPORT_SAML_MANUALLY?.x509Certificate?.variant) }>
                    {
                        (() => {
                            const message = validations.IMPORT_SAML_MANUALLY?.x509Certificate?.message?.trim();
                            if (message) {
                                // we need to treat the x509 message as an array because it has multiple messages
                                return message.split('@')
                                    .map((msg, index) => (
                                        <p key={index} dangerouslySetInnerHTML={{__html: msg}}/>
                                    ));
                            }
                            // if there is no validation message to throw, show the default helper text message defined in the constant
                            return SamlHelperTextMessages.X509_CERTIFICATE.map((msg, index) => (
                                <p key={index} dangerouslySetInnerHTML={{__html: msg}}/>
                            ));
                        })()
                    }
                </HelperTextItem>
            </HelperText>
        </FormGroup>

    </Form>

}

export default ImportSamlManual;
