import React from "react";
import { Button, Modal, ModalVariant } from "@patternfly/react-core";
import { apiDeleteIdp } from "../ApiClient/idp-controller";
import { useChrome } from "@redhat-cloud-services/frontend-components/useChrome";
import { ModalBody, ModalFooter } from "@patternfly/react-core/next";

interface Props {
  idpAlias: string,
  isOpen: boolean,
  onClose: () => void,
  recomputeAppState: () => void
}

export const DeleteIdpModal = ({ idpAlias, isOpen, onClose, recomputeAppState }: Props) => {
  const chrome = useChrome();

  const deleteIdp = async () => {
    await apiDeleteIdp(chrome, idpAlias);
    recomputeAppState();
    onClose();
  }

  return (
    <Modal
      titleIconVariant={'warning'}
      title="Delete identity provider?"
      isOpen={isOpen}
      variant={ModalVariant.small}
      onClose={onClose}
    >
      <ModalBody>
        You will have to restart the wizard to configure an identity provider.
      </ModalBody>
      <ModalFooter>
        <Button variant="danger" onClick={() => deleteIdp()}>
          Delete
        </Button>
        <Button variant="link" onClick={onClose}>
          Cancel
        </Button>
      </ModalFooter>
    </Modal>
  )
}

