import {Button, Modal, ModalVariant, Text, TextContent, Title} from "@patternfly/react-core";
import React, {useEffect, useState} from "react";
import { getSSOBaseUrl } from "../../../Utils/general-utils";
import {useChrome} from "@redhat-cloud-services/frontend-components/useChrome";
import {Link} from "react-router-dom";
import {apiToggleIdPStatus} from "../ApiClient/idp-controller";

interface Props {
    idpAlias: string,
    isOpen: boolean,
    onClose: () => void,
    recomputeAppState: () => void
}

const IdpTestingModal = ({ idpAlias, isOpen, onClose, recomputeAppState }: Props) => {
    const chrome = useChrome();
    const ssoBaseUrl = getSSOBaseUrl(chrome);

    const [primaryModalAction, setPrimaryModalAction] = useState(<Button />);
    const [testBtnEnabled, setTestBtnEnabled] = useState(false);
    const [idpTestingModalIcon, setIdpTestingModalIcon] = useState<"info"|"danger"|"success">("info");
    const [idpTestingModalTitle, setIdpTestingModalTitle] = useState("");
    const [idpTestingModalBody, setIdpTestingModalBody] = useState(<Text></Text>);
    const [idpTestingModalErrorBody, setIdpTestingModalErrorBody] = useState("");

    const resetTestingModal = () => {
        setPrimaryModalAction(<Button />);
        setTestBtnEnabled(true);
        setIdpTestingModalIcon("info");
        setIdpTestingModalTitle("Identity provider testing");
        setIdpTestingModalBody(<Text>Testing will open a pop-up window that will authenticate with your configured identity provider. If you are not currently signed into this identity provider, please do so in the pop-up window. Please be sure that pop-ups are allowed in your browser.</Text>);
        setIdpTestingModalErrorBody("");
    }

    useEffect(() => {
        resetTestingModal();
    }, []);

    const dismissTestingModal = () => {
        onClose();
        resetTestingModal();
    }

    const getTestUrl = () => {
        const idpAliasJson = { 'idp_alias': idpAlias }
        const idpAliasJsonString = encodeURIComponent(JSON.stringify(idpAliasJson));
        return `${ssoBaseUrl}protocol/openid-connect/auth?client_id=idp-connectivity-testing-login-client&redirect_uri=${ssoBaseUrl}&response_mode=query&response_type=code&scope=openid&state=${idpAliasJsonString}`;
    }

    const enableIdP = async () => {
        await apiToggleIdPStatus(chrome, idpAlias);
        recomputeAppState();
        dismissTestingModal();
    }

    const handleTestBtn = () => {
        setTestBtnEnabled(false);
        setIdpTestingModalBody(<Text>Please complete identity provider testing in pop-up window</Text>);

        window.open(getTestUrl(), "RH IdP Integration Testing", 'width=600,height=600,resizable,scrollbars');

        window.addEventListener("message", (event) => {
            if (ssoBaseUrl.includes(event.origin)) {
                if (event.data.success === true) {
                    setPrimaryModalAction(<Button key="Enable" variant="primary" onClick={enableIdP}>Enable</Button>)
                    setIdpTestingModalIcon("success")
                    setIdpTestingModalTitle("Test successful. Enable for your organization?")
                    setIdpTestingModalBody(<Text>Enabling will require users on your organization to authenticate through your newly-configured identity provider on their next authentication. If you do not enable this now, you will have to re-test again before being allowed to enable this identity provider.</Text>)
                } else {
                    setPrimaryModalAction(<Button key="Dismiss" variant="primary" onClick={dismissTestingModal}>Dismiss</Button>)
                    setIdpTestingModalIcon("danger")
                    setIdpTestingModalTitle("Test failed")
                    setIdpTestingModalBody(<Text>Try looking over some <Link to="https://access.redhat.com/articles/7092870">common mistakes</Link> and then re-testing. If that doesnt help, <Link to={"https://access.redhat.com/support/"}>contact support</Link>.</Text>)
                    setIdpTestingModalErrorBody(event.data.errorMsg);
                }
            }
        });
    }

    return (
        <Modal
            titleIconVariant={idpTestingModalIcon}
            title={idpTestingModalTitle}
            isOpen={isOpen}
            variant={ModalVariant.medium}
            onClose={dismissTestingModal}
            actions={[
                primaryModalAction.key !== null ? primaryModalAction
                : [
                    <Button isDisabled={!testBtnEnabled} key="Test" variant="primary" onClick={handleTestBtn}>
                        Test
                    </Button>,
                    <Button key="cancel" variant="link" onClick={dismissTestingModal}>
                        Cancel
                    </Button>
                ]
            ]}
        >
            <TextContent>
                <Text>{idpTestingModalBody}</Text>
                <Title headingLevel={"h3"} hidden={!idpTestingModalErrorBody}>Error:</Title>
                <Text hidden={!idpTestingModalErrorBody}>{idpTestingModalErrorBody}</Text>
            </TextContent>
        </Modal>
    )
}

export { IdpTestingModal };
