import React from 'react';
import { WizardFooterWrapper} from '@patternfly/react-core/dist/esm/components/Wizard';
import { Button, Checkbox, Stack, StackItem } from '@patternfly/react-core';

const FinalizeFooter = () => {

    const [checkState, setCheckState] = React.useState(false);
    /**
     * Governs what happens when the next button gets clicked. Important for allowing the back and forth communication
     * between the footer element and the actual component being displayed when needing to perform steps such as validation.
     */
    const handleNextClick = () => {
        window.location.reload();
    }

    /**
     * handled
     */
    const handleCheckClick = () => {
        setCheckState(!checkState);
    }

    return (
        <WizardFooterWrapper>
            <Stack hasGutter>
                <StackItem>
                    <Checkbox onChange={handleCheckClick} label="I have verified that this information is properly set in my identity provider." id="confirmation" isChecked={checkState} isRequired></Checkbox>
                </StackItem>
                <StackItem>
                    <Button onClick={handleNextClick} isDisabled={!checkState}>Submit</Button>
                </StackItem>
            </Stack>
        </WizardFooterWrapper>
    );
}

export default FinalizeFooter;
